import androidx.compose.foundation.layout.Column
import androidx.compose.material3.Text
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.ui.ExperimentalComposeUiApi
import androidx.compose.ui.window.CanvasBasedWindow
import com.arkivanov.decompose.DefaultComponentContext
import com.arkivanov.decompose.ExperimentalDecomposeApi
import com.arkivanov.decompose.router.stack.webhistory.DefaultWebHistoryController
import com.arkivanov.essenty.lifecycle.LifecycleRegistry
import com.arkivanov.essenty.lifecycle.resume
import com.arkivanov.essenty.lifecycle.stop
import js.import.import.meta
import kotlinx.browser.window
import kotlinx.coroutines.delay
import navigation.DeepLink
import navigation.DefaultRootComponent
import org.jetbrains.skiko.wasm.onWasmReady
import web.dom.DocumentVisibilityState
import web.dom.document
import web.events.EventHandler

@OptIn(ExperimentalComposeUiApi::class, ExperimentalDecomposeApi::class)
fun main() {
    val lifecycle = LifecycleRegistry()
    val rootComponent = DefaultRootComponent(
        componentContext = DefaultComponentContext(lifecycle),
        webHistoryController = DefaultWebHistoryController(),
        deepLink = DeepLink.Web(getPathname())
    )
    lifecycle.attachToDocument()

    onWasmReady {
        CanvasBasedWindow(canvasElementId = "ComposeTarget") {
            LaunchedEffect(Unit) {
                delay(100L)
                onLoadFinished()
            }
            App(rootComponent)
        }
    }
}

fun getPathname(): String {
    return js("window.location.pathname")
}

private fun LifecycleRegistry.attachToDocument() {
    document.onvisibilitychange = EventHandler {
        if (document.visibilityState == DocumentVisibilityState.visible) {
            resume()
        } else {
            stop()
        }
    }
}

fun onLoadFinished() = js("onLoadFinished()")
